import React, { useEffect, useRef, useState } from "react";
import Peer from "peerjs";
import { database, saveUserId, removeUserId, fetchActiveUsers } from "./firebase";
import { ref, onValue, set, serverTimestamp } from "firebase/database";
import "./VideoCall.css";

const VideoCall = () => {
  const [peerId, setPeerId] = useState("");
  const [activeUsers, setActiveUsers] = useState(0);
  const [waiting, setWaiting] = useState(false);
  const [noUsersAvailable, setNoUsersAvailable] = useState(false);
  const [connected, setConnected] = useState(false);
  const [permissionStatus, setPermissionStatus] = useState("pending"); // "pending", "granted", "denied", "insecure_context"
  const [connectionError, setConnectionError] = useState(null);
  const [accessType, setAccessType] = useState("unknown"); // "localhost", "ip", "domain"
  const localVideoRef = useRef(null);
  const remoteVideoRef = useRef(null);
  const peer = useRef(null);
  const localStream = useRef(null);
  const currentCall = useRef(null);
  const heartbeatInterval = useRef(null);

  // Detect how the user is accessing the app
  useEffect(() => {
    const host = window.location.hostname;
    if (host === 'localhost' || host === '127.0.0.1') {
      setAccessType("localhost");
    } else if (/^(\d{1,3}\.){3}\d{1,3}$/.test(host)) {
      setAccessType("ip");
    } else {
      setAccessType("domain");
    }
    
    // Check if we're in a secure context
    if (!window.isSecureContext) {
      console.warn("Not running in a secure context. Media permissions may be denied.");
    }
  }, []);

  // Setup media stream
  const setupMediaStream = async () => {
    try {
      // Try to get user media with a more permissive approach first
      const constraints = { 
        video: { facingMode: "user" }, 
        audio: true 
      };
      
      console.log("Requesting media access with constraints:", constraints);
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      
      localStream.current = stream;
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
      setPermissionStatus("granted");
      return stream;
    } catch (error) {
      console.error("Error accessing media devices:", error);
      
      // Check if this might be a secure context issue
      if (window.location.protocol !== 'https:' && accessType === "ip" && 
          (error.name === 'NotAllowedError' || error.name === 'PermissionDeniedError')) {
        setPermissionStatus("insecure_context");
      } else {
        setPermissionStatus("denied");
        alert("Error accessing camera/microphone: " + error.message);
      }
      return null;
    }
  };

  // Start heartbeat to keep user active in database
  const startHeartbeat = (id) => {
    // Clear any existing heartbeat
    if (heartbeatInterval.current) {
      clearInterval(heartbeatInterval.current);
    }
    
    // Send heartbeat every 10 seconds
    heartbeatInterval.current = setInterval(() => {
      const userRef = ref(database, `users/${id}`);
      set(userRef, { 
        available: true,
        lastSeen: serverTimestamp()
      }).catch(err => console.error("Heartbeat error:", err));
    }, 10000);
    
    // Send initial heartbeat
    const userRef = ref(database, `users/${id}`);
    set(userRef, { 
      available: true,
      lastSeen: serverTimestamp()
    }).catch(err => console.error("Initial heartbeat error:", err));
  };

  // Request permissions before starting the app
  const requestPermissions = async () => {
    setPermissionStatus("requesting");
    const result = await setupMediaStream();
    if (result) {
      // Initialize PeerJS only after permissions are granted
      initializePeerJS();
    }
  };

  // Initialize PeerJS
  const initializePeerJS = () => {
    // Initialize PeerJS with more ice servers for better connectivity
    peer.current = new Peer(undefined, {
      debug: 3, // Increased debug level
      config: {
        iceServers: [
          { urls: "stun:stun.l.google.com:19302" },
          { urls: "stun:stun1.l.google.com:19302" },
          { urls: "stun:stun2.l.google.com:19302" },
          { urls: "stun:stun3.l.google.com:19302" },
          { urls: "stun:stun4.l.google.com:19302" },
          { urls: "stun:stun.ekiga.net" },
          { urls: "stun:global.stun.twilio.com:3478" },
          {
            urls: 'turn:numb.viagenie.ca',
            credential: 'muazkh',
            username: 'webrtc@live.com'
          },
          {
            urls: 'turn:turn.anyfirewall.com:443?transport=tcp',
            credential: 'webrtc',
            username: 'webrtc'
          }
        ]
      }
    });

    peer.current.on("open", async (id) => {
      console.log("My peer ID is:", id);
      setPeerId(id);
      
      // Instead of just saving once, start the heartbeat
      await saveUserId(id);
      startHeartbeat(id);
    });

    peer.current.on("error", (err) => {
      console.error("PeerJS error:", err);
      
      // Handle peer unavailable error differently
      if (err.type === 'peer-unavailable') {
        console.log("The peer you're trying to connect to is unavailable");
        setConnectionError("The user you're trying to connect to is unavailable or offline.");
        setWaiting(false);
        
        // Try to find another user automatically after a brief delay
        setTimeout(() => {
          setConnectionError(null);
          if (!connected) {
            findChat();
          }
        }, 2000);
      } else {
        alert(`Connection error: ${err.type} - ${err.message}`);
      }
    });

    peer.current.on("disconnected", () => {
      console.log("Disconnected from server, attempting to reconnect");
      peer.current.reconnect();
    });

    peer.current.on("call", async (call) => {
      console.log("Receiving call from:", call.peer);
      currentCall.current = call;
      setWaiting(false);
      setNoUsersAvailable(false);
      setConnectionError(null);
      setConnected(true);
      
      try {
        // If we don't have a stream yet, get one
        if (!localStream.current) {
          localStream.current = await setupMediaStream();
        }
        
        // Answer the call with our stream
        call.answer(localStream.current);
        
        // Handle the incoming stream
        call.on("stream", (remoteStream) => {
          console.log("Received remote stream");
          if (remoteVideoRef.current) {
            remoteVideoRef.current.srcObject = remoteStream;
          }
        });

        call.on("close", () => {
          console.log("Call closed");
          setConnected(false);
          if (remoteVideoRef.current) {
            remoteVideoRef.current.srcObject = null;
          }
        });

        call.on("error", (err) => {
          console.error("Call error:", err);
          setConnected(false);
        });
      } catch (error) {
        console.error("Error during call:", error);
        alert("Error during call: " + error.message);
      }
    });

    // Listen for active users
    const usersRef = ref(database, "users");
    onValue(usersRef, (snapshot) => {
      if (snapshot.exists()) {
        // Filter out potentially stale users (no heartbeat in last 30 seconds)
        const userData = snapshot.val();
        const activeUserIds = Object.keys(userData).filter(userId => {
          const user = userData[userId];
          // If lastSeen is not available, assume user is active
          if (!user.lastSeen) return true;
          
          // Calculate time since last seen (if available)
          const lastSeen = user.lastSeen;
          const now = Date.now();
          // Consider user inactive if no heartbeat in 30 seconds
          return (now - lastSeen) < 30000;
        });
        
        setActiveUsers(activeUserIds.length);
        console.log("Active users updated:", activeUserIds);
      } else {
        setActiveUsers(0);
      }
    });
  };

  useEffect(() => {
    // We don't automatically initialize now - we wait for permission grant
    
    return () => {
      // Clean up
      if (heartbeatInterval.current) {
        clearInterval(heartbeatInterval.current);
      }
      
      if (peerId) {
        removeUserId(peerId);
      }
      if (currentCall.current) {
        currentCall.current.close();
      }
      if (localStream.current) {
        localStream.current.getTracks().forEach(track => track.stop());
      }
      if (peer.current) {
        peer.current.destroy();
      }
    };
  }, []);

  // Find a random available user and call them
  const findChat = async () => {
    setWaiting(true);
    setNoUsersAvailable(false);
    setConnectionError(null);
    
    // Ensure we have media access
    if (!localStream.current) {
      localStream.current = await setupMediaStream();
      if (!localStream.current) {
        setWaiting(false);
        return;
      }
    }

    // Close any existing call
    if (currentCall.current) {
      currentCall.current.close();
      currentCall.current = null;
    }

    try {
      // Get available users excluding ourselves
      const availableUsers = (await fetchActiveUsers()).filter((id) => id !== peerId);
      console.log("Available users to call:", availableUsers);

      if (availableUsers.length > 0) {
        // Try random users until one connects or we run out
        const shuffledUsers = [...availableUsers].sort(() => 0.5 - Math.random());
        callNextUser(shuffledUsers);
      } else {
        console.log("No users available");
        setWaiting(false);
        setNoUsersAvailable(true);
      }
    } catch (error) {
      console.error("Error finding chat:", error);
      setWaiting(false);
      setConnectionError("Error finding users: " + error.message);
    }
  };

  // Call users one by one until successful
  const callNextUser = (userIds, index = 0) => {
    if (index >= userIds.length) {
      // We've tried all users and none worked
      setWaiting(false);
      setNoUsersAvailable(true);
      return;
    }

    const userId = userIds[index];
    console.log(`Attempting to call user ${index + 1}/${userIds.length}: ${userId}`);
    
    try {
      // Make the call with our stream
      const call = peer.current.call(userId, localStream.current);
      currentCall.current = call;
      
      // Set a timeout for this particular call attempt
      const callTimeout = setTimeout(() => {
        if (!connected && currentCall.current === call) {
          console.log(`Call to ${userId} timed out, trying next user`);
          if (!call.closed) call.close();
          // Try the next user
          callNextUser(userIds, index + 1);
        }
      }, 5000); // 5 second timeout per call attempt
      
      call.on("stream", (remoteStream) => {
        console.log("Received stream from called peer");
        clearTimeout(callTimeout); // Clear the timeout as we've connected
        
        if (remoteVideoRef.current) {
          remoteVideoRef.current.srcObject = remoteStream;
        }
        setWaiting(false);
        setConnected(true);
      });

      call.on("close", () => {
        console.log("Call closed");
        clearTimeout(callTimeout);
        setConnected(false);
        if (remoteVideoRef.current) {
          remoteVideoRef.current.srcObject = null;
        }
      });

      call.on("error", (err) => {
        console.error("Call error:", err);
        clearTimeout(callTimeout);
        // Try the next user
        callNextUser(userIds, index + 1);
      });
    } catch (error) {
      console.error(`Error calling peer ${userId}:`, error);
      // Try the next user
      callNextUser(userIds, index + 1);
    }
  };

  // Render insecure context warning (when accessed via IP)
  if (permissionStatus === "insecure_context") {
    return (
      <div className="permission-insecure-container">
        <h2>Security Warning</h2>
        <p>You're accessing this app through an IP address without HTTPS.</p>
        <p>Most browsers require a secure context (HTTPS) for camera and microphone access.</p>
        
        <div className="access-options">
          <h3>To use this app, try one of these methods:</h3>
          <ol>
            <li>
              <strong>Access via localhost:</strong>
              <p>If running locally, use <a href="http://localhost:3000">http://localhost:3000</a> instead of the IP address</p>
            </li>
            <li>
              <strong>Try these browser settings:</strong>
              <ul>
                <li>
                  <strong>Chrome:</strong> Go to <code>chrome://flags/#unsafely-treat-insecure-origin-as-secure</code> and add your IP address
                </li>
                <li>
                  <strong>Firefox:</strong> Go to <code>about:config</code> and set <code>media.devices.insecure.enabled</code> and <code>media.getusermedia.insecure.enabled</code> to <code>true</code>
                </li>
              </ul>
            </li>
            <li>
              <strong>Force Permission Request:</strong>
              <p>Click the button below to try requesting permissions anyway (may not work in all browsers)</p>
              <button className="force-permission-button" onClick={requestPermissions}>
                Force Permission Request
              </button>
            </li>
          </ol>
        </div>
      </div>
    );
  }

  // Render permission request screen
  if (permissionStatus === "pending" || permissionStatus === "requesting") {
    return (
      <div className="permission-container">
        <h2>Camera and Microphone Access Required</h2>
        <p>This app needs access to your camera and microphone to enable video chat.</p>
        
        {accessType === "ip" && (
          <div className="permission-warning">
            <p> You're accessing this app through an IP address, which might cause permission issues in some browsers.</p>
            <p>If permissions fail, try accessing via <code>localhost</code> instead of the IP address.</p>
          </div>
        )}
        
        <button 
          className="permission-button" 
          onClick={requestPermissions}
          disabled={permissionStatus === "requesting"}
        >
          {permissionStatus === "requesting" ? "Requesting Access..." : "Allow Access"}
        </button>
        <div className="permission-info">
          <p>Why we need these permissions:</p>
          <ul>
            <li>Camera: To show your video to other users</li>
            <li>Microphone: To transmit your voice to other users</li>
          </ul>
          <p>You can revoke these permissions at any time through your browser settings.</p>
        </div>
      </div>
    );
  }

  // Render denied permission screen
  if (permissionStatus === "denied") {
    return (
      <div className="permission-denied-container">
        <h2>Permission Denied</h2>
        <p>Camera and/or microphone access was denied. This app cannot function without these permissions.</p>
        
        {accessType === "ip" && (
          <div className="permission-warning">
            <p> You're accessing this app via an IP address, which can cause permission issues in some browsers.</p>
            <p>Try accessing the app using <code>localhost</code> instead of an IP address.</p>
          </div>
        )}
        
        <p>Please check your browser settings and make sure you've allowed camera/microphone permissions for this site.</p>
        <button className="permission-button" onClick={() => window.location.reload()}>
          Try Again
        </button>
      </div>
    );
  }

  // Main app UI (only shown after permissions granted)
  return (
    <div className="video-call-container">
      <div className="active-users-counter">Active Users: {activeUsers}</div>
      <h2>Your Peer ID: {peerId}</h2>
      <button 
        className="find-chat-button" 
        onClick={findChat} 
        disabled={waiting || connected}
      >
        {waiting ? "Finding Chat..." : connected ? "Connected" : "Find a Chat"}
      </button>
      {waiting && <p className="waiting-text">Waiting for a user...</p>}
      {noUsersAvailable && <p className="no-users-text">No users available. Try again later.</p>}
      {connectionError && <p className="error-text">{connectionError}</p>}
      <div className="video-container">
        <video ref={localVideoRef} autoPlay muted playsInline className="video" />
        <video ref={remoteVideoRef} autoPlay playsInline className="video" />
      </div>
      {connected && (
        <button 
          className="end-call-button" 
          onClick={() => {
            if (currentCall.current) {
              currentCall.current.close();
              currentCall.current = null;
            }
            setConnected(false);
          }}
        >
          End Call
        </button>
      )}
    </div>
  );
};

export default VideoCall;
