import { initializeApp } from "firebase/app";
import { getDatabase, ref, set, remove, get, child, serverTimestamp } from "firebase/database";

const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID,
  databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
};

const app = initializeApp(firebaseConfig);
const database = getDatabase(app);

//  Save user ID in the database with timestamp
export const saveUserId = async (userId) => {
  try {
    await set(ref(database, `users/${userId}`), { 
      available: true,
      lastSeen: serverTimestamp()
    });
    console.log("User ID saved successfully:", userId);
  } catch (error) {
    console.error("Error saving user ID:", error);
  }
};

//  Remove user ID when they leave
export const removeUserId = async (userId) => {
  try {
    await remove(ref(database, `users/${userId}`));
    console.log("User ID removed successfully:", userId);
  } catch (error) {
    console.error("Error removing user ID:", error);
  }
};

//  Fetch active users with filtering for stale users
export const fetchActiveUsers = async () => {
  try {
    const snapshot = await get(child(ref(database), "users"));
    if (!snapshot.exists()) {
      return [];
    }
    
    const userData = snapshot.val();
    // Filter out users who haven't sent a heartbeat in the last 30 seconds
    const activeUsers = Object.keys(userData).filter(userId => {
      const user = userData[userId];
      // If no lastSeen property, assume user is active
      if (!user.lastSeen) return true;
      
      // Calculate if the user is still active based on timestamp
      const lastSeen = user.lastSeen;
      const now = Date.now();
      return (now - lastSeen) < 30000; // 30 seconds
    });
    
    console.log("Fetched active users:", activeUsers);
    return activeUsers;
  } catch (error) {
    console.error("Error fetching users:", error);
    return [];
  }
};

export { database, serverTimestamp };
